.container {
    margin: 0 auto;
    padding: 0 20px;

    @media screen and (min-width:320px) {
        width: 100%;

    }

    @media screen and (min-width:480px) {
        width: 480px;

    }

    @media screen and (min-width:768px) {
        width: 768px;
        padding: 0 32px;
    }

    @media screen and (min-width:1280px) {
        width: 1280px;
        padding: 0 16px;
    }

}