.favorites {
  &__container{
  padding-top: 60px;
  padding-bottom: 60px;

  @media screen and (min-width: 768px) {
    padding-top: 100px;
    padding-bottom: 100px;
  }}
  ////    /////
  &__title {
    font-size: 28px;
    line-height: 1.17;
    text-align: center;

    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
      font-size: 42px;
      line-height: 1.16;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 1.14;
    text-align: center;
    margin-bottom: 60px;

    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: 1.8;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 40px;
    }
  }

  //////   \\\\\
  &__list-mob {
    @media screen and (min-width: 768px) {
      display: none;
    }

    display: flex;
    flex-direction: column;
    gap: 20px;
    

  }

  &__item-mob {
      justify-content: center;
        display: flex;
  }

  


  &__img {
    padding: 20px;

    border: 11px solid #ffe550;
    border-radius: 50%;

    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 768px) {
      border: 14px solid #ffe550;
     
    }

    @media screen and (min-width: 1280px) {
      border: 16px solid #ffe550;
    
    }
  }

  &__list {
    @media screen and (max-width: 768px) {
      display: none;
    }

  @media screen and (min-width: 768px) {
  display: flex;
  flex-wrap: wrap;
  gap: 31px;
  justify-content: space-between;
}

  }



  /////  //////
  .favorites__link:hover .favorites__img,
  .favorites__link:focus .favorites__img,
  .favorites__link:active .favorites__img {
    border: 11px solid var(--hover-button-color);
    scale: 1.1;

    @media screen and (min-width: 768px) {
      border: 14px solid var(--hover-button-color);
    }

    @media screen and (min-width: 1280px) {
      border: 16px solid var(--hover-button-color);
    }
  }
}
